<template>
    <div class="sms">
        <van-swipe
            v-if="data.invitationCarousel.length"
            :autoplay="3000"
            vertical
            :show-indicators="false">
            <van-swipe-item
                v-for="(item, i) in data.invitationCarousel"
                :key="'swipe' + i">
                <span>{{ item.user }}邀请了{{ item.discipleNum }}名好友</span>
            </van-swipe-item>
        </van-swipe>
        <div class="info">
            <img :src="data.headIcon">
            {{ userName }}邀你赚<span><b>400</b>元话费</span>
        </div>
        <div class="code">
            {{ $route.query.shareId }}
            <div
                class="copy"
                @click="download">
                复制前往
            </div>
        </div>
        <div
            class="btn scale-infinite"
            @click="download">
            点击下载领取
        </div>
        <p class="footer">温馨提示：下载后微信登录且绑定手机号立领4元红包</p>
    </div>
</template>

<script>
import { invitationShare } from '@/api/invitation';
import { copy } from '@/utils/string';
import { tryDeeplink, goBrowerDownload, downloadChannelApk } from '@/utils/share';
import { isWechat } from '@/utils/ua';
import config from '@/config';
import SwipeList from './components/swipe-list';

export default {
    name: 'SMSPage',
    components: {
        SwipeList,
    },
    data() {
        return {
            data: {
                invitationCarousel: [],
            },
            schema: `bikan://goto/commonWebView?url=${encodeURIComponent(`${window.location.origin}/mobile-v2/act/invitation/sms`)}`,
        };
    },
    computed: {
        userName() {
            if (this.data.user && this.data.user.length > 7) {
                return `${this.data.user.slice(0, 6)}...`;
            }
            return this.data.user || '想看用户';
        },
    },
    beforeCreate() {
        if (isWechat()) {
            this.$router.replace({
                name: 'invitationNewShare',
                query: this.$route.query,
            });
        }
    },
    created() {
        // deeplink
        tryDeeplink(this.schema);
        invitationShare({ invitationCode: this.$route.query.shareId }).then((res) => {
            if (res.data.status === 200) {
                this.data = res.data.data;
                this.$root.hidePlaceholder();
                this.$o2o({
                    e_c: '短信邀请',
                    e_a: '曝光',
                    e_n: '短信邀请落地页',
                });
            } else {
                this.$error();
            }
        }).catch(() => {
            this.error();
        });
    },
    methods: {
        copy() {
            copy(this.$route.query.shareId);
            this.$o2o({
                e_c: '短信邀请',
                e_a: '点击',
                e_n: '复制邀请码',
            });
        },
        download() {
            this.copy();
            this.$o2o({
                e_c: '短信邀请',
                e_a: '点击',
                e_n: '立即下载提现',
            });
            tryDeeplink(config.StoreDeeplink, {
                fail: () => {
                    if (isWechat()) {
                        goBrowerDownload({
                            schema: this.schema,
                        });
                    } else {
                        downloadChannelApk('xiangkan_invitation.apk');
                    }
                },
            });
        },
    },
};
</script>

